import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/home/Home'
import WechatGuide from '@/pages/home/WechatGuide'
import Guide2 from '@/pages/home/guide2'
import City from '@/pages/city/City'
import Detail from '@/pages/detail/Detail'
import TryApp1 from '@/pages/home/TryAppPage1'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/tryapp1',
      name: 'TryApp1',
      component: TryApp1
    },
    {
      path: '/WechatGuide',
      name: 'WechatGuide',
      component: WechatGuide
    },
    {
      path: '/guide2',
      name: 'Guide2',
      component: Guide2
    },
    {
      path: '/city',
      name: 'City',
      component: City
    },
    {
      path: '/detail/:id',
      name: 'Detail',
      component: Detail
    }
  ],
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})



