<template>
    <div>
        <div class="content">
            <div class="picture-image-message" v-if="showMessage1">
                <img class="header-icon"  src="../../../static/moc/header_icon.jpg" alt="">
                <img class="gif"  src="../../../static/moc/biaoqing.gif" alt="">
            </div>

            <div class="messageItem" v-if="showMessage2">
                <img class="header-icon"  src="../../../static/moc/header_icon.jpg" alt="">
                <span class="arrow"></span>
                <div class="text-message">  
                    <img class="arrow" src="../../../static/moc/arrow.png" alt="">
                    <span>11 1title title title title title title ti</span>
                </div>
                
            </div>

            <div class="messageItem" v-if="showMessage3">
                <img class="header-icon"  src="../../../static/moc/header_icon.jpg" alt="">
                <span class="arrow"></span>
                <div class="text-message">  
                    <img class="arrow" src="../../../static/moc/arrow.png" alt="">
                    <span>11 1title title title title title title ti</span>
                </div>
                
            </div>
        </div>
         <div class="button" v-on:click="btnClick" v-if="isWechat == true">
                点击加入
        </div>
        <div class="guide-hover" v-show="showHover" v-on:click="funcHideHover">
            <img v-if="isIos" class="guide-img" src="../../../static/moc/ios_guide.png" alt="">
            <img v-if="isAndroid" class="guide-img" src="../../../static/moc/android_guide.png" alt="">
            
        </div>
    </div>

    
</template>

<script>


export default {
  name: 'Guide',
  data() {
    return {
      isWechat : false,
      isIos : false,
      isAndroid : true,
      showMessage1 : false,
      showMessage2 : false,
      showMessage3 : false,
      showHover : false
    };
  },
  methods : {
      funcShowMessage1() {
          this.showMessage1 = true;
      },
      funcShowMessage2() {
          this.showMessage2 = true;
      },
      funcShowMessage3() {
          this.showMessage3 = true;
      },
      btnClick() {
          this.showHover = true;
      },
      funcHideHover() {
          this.showHover = false;
      }
  },
  mounted() {
      console.log('ddddd');
      const u = navigator.userAgent;
      var isWechat = u.indexOf('MicroMessenger') > -1;
      var ios = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); // ios终端
      var android = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; // android终端
      this.isWechat = isWechat;
      if(this.isWechat == false) {
          this.isWechat = true;
      }
      setTimeout(this.funcShowMessage1,1000);
      setTimeout(this.funcShowMessage2,3000);
      setTimeout(this.funcShowMessage3,4500);
  }
  
}
</script>

<style lang="stylus" scoped >
    .guide-hover
        z-index 999
        position fixed
        top 0
        left 0
        bottom 0
        right 0
        background rgba(0, 0, 0, 0.7)
        .guide-img
            width 4rem
            right 0.2rem
            top  0.2rem
            position absolute            
    .button
        position fixed
        height 1rem
        width 100%
        bottom 0rem
        background-color #EF502B
        line-height 1rem
        text-align center
        color white
        font-size 15px
    .content
       position  absolute
       background-color #f5f5f5 
       width  100%
       height 100%
       top 0
       bottom 0
       right 0
       left 0
       .picture-image-message
            margin-top 0.5rem
            margin-left 0.2rem
            // display inline-block
            position relative
            width 100%
            .header-icon
                position absolute
                top 0rem
                height 1rem
                width 1rem
                border-radius 10%
            .gif
                height 2rem
                margin-left 1rem
       .messageItem
            margin-top 0.5rem
            margin-left 0.2rem
            // display inline-block
            position relative
            width 100%
            color black 
            height 1rem
            .header-icon
                position absolute
                top 0rem
                height 1rem
                width 1rem
                border-radius 10%
            .gif
                height 2rem
                margin-left 1rem
            .text-message
                position absolute
                top 0rem
                bottom 0rem
                left 1rem
                right 0rem
                display inline-block
                line-height  1rem
                height 1rem
                span 
                    background-color white
                    display inline-block
                    padding-left 0.2rem
                    padding-right 0.2rem
                    border-radius 0.15rem
                    margin-left -0.15rem
                .arrow
                    margin-left 0.1rem
                    width 0.3rem
                    height 0.3rem
                       

          
</style>



